import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { useDispatch } from "react-redux";
import { IExpense } from "../../Models/ExpenseModel";

export interface expenseListState {
  expenseList: IExpense[];
}

const initialState: expenseListState = {
  expenseList: [],
};

export const expenseListSlice = createSlice({
  name: "expenses",
  initialState,
  reducers: {
    setExpenseList: (state, action: PayloadAction<IExpense[]>) => {
      state.expenseList = action.payload;
    },
    addExpense: (state, action: PayloadAction<IExpense>) => {
      state.expenseList.push(action.payload);
    },
    updateExpense: (state, action: PayloadAction<IExpense>) => {
      const indexToUpdate = state.expenseList.findIndex(
        (t) => t.id === action.payload.id
      );
      if (indexToUpdate >= 0) state.expenseList[indexToUpdate] = action.payload;
    },
    deleteExpense: (state, action: PayloadAction<number>) => {
      const indexToDelete = state.expenseList.findIndex(
        (t) => t.id === action.payload
      );
      if (indexToDelete >= 0) state.expenseList.splice(indexToDelete, 1);
    },
  },
});

export const { setExpenseList, addExpense, updateExpense, deleteExpense } =
  expenseListSlice.actions;

export const getExpenses = (state: any) => {
  state.expenseListState.expenseList();
};

export default expenseListSlice.reducer;
