import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export enum clientEnum {
  "USER",
  "ADMIN",
}

export interface clientAuth {
  email: string;
  firstName: string;
  lastName: string;
  client: clientEnum;
  token: string;
}

export interface AuthState extends clientAuth {
  isLoggedIn: boolean;
}

const initialState: AuthState = {
  isLoggedIn: false,
  email: "",
  firstName: "",
  lastName: "",
  client: clientEnum.USER,
  token: "",
};

export const authSlice = createSlice({
  name: "auth",
  initialState,
  reducers: {
    login: (state, action: PayloadAction<AuthState>) => {
      state.isLoggedIn = true;
      state.token = action.payload.token;
      state.client = action.payload.client;
      state.firstName = action.payload.firstName;
      state.lastName = action.payload.lastName;
      state.email = action.payload.email;
    },
    logout: (state) => {
      //this method is used by the root reducer to clear the app state when logging out
    },
  },
});

export const { login, logout } = authSlice.actions;

export const isLoggedIn = (state: any) => state.AuthState.isLoggedIn;

export default authSlice.reducer;
